import DeepData from "../../assets/js/formate.js";
export default {
  name: "agent",
  data() {
    return {
      orderlist: ["ascending", "descending"],
      //代理数据
      userID: "",
      start_time: "",
      end_time: "",
      currentPage: 1,
      limit: 10,
      total: "",
      tableData: [],
      //代理详情数据
      DuserID: "",
      Dstart_time: "",
      Dend_time: "",
      DcurrentPage: 1,
      Dlimit: 10,
      Dtotal: "",
      DtableData: [],
      //下级
      LcurrentPage: 1,
      Llimit: 10,
      title: "详情",
      visible: false
    };
  },
  created() {
    this.start_time = (new Date(), new Date().getTime()) - 60 * 60 * 24 * 7 * 1000;
    this.end_time = new Date().getTime();
    this.initdata({ page: this.currentPage, limit: this.limit });
  },
  methods: {
    Closed(){
      this.DuserID = '';
      this.title = '详情'
    },
    //查找
    search() {
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        uid: parseInt(this.userID) || 0,
        start_time: Math.ceil(this.start_time / 1000) || 0,
        end_time:  Math.ceil(this.end_time / 1000) || 0
      });
    },

    //详情 / 下级 搜索
    searchDetail(type) {
      if (type === "详情") {
        this.initDetail({
          page: this.DcurrentPage,
          limit: this.Dlimit,
          pid: this.Pid,
          start_data: Math.ceil(this.Dstart_time / 1000) || 0,
          end_data: Math.ceil(this.Dend_time / 1000) || 0,
          uid: parseInt(this.DuserID) || 0
        });
      } else if (type === "下级") {
        this.initLevel({
          uid: parseInt(this.DuserID) || 0,
          pid: this.Pid,
          start_data: Math.ceil(this.Dstart_time / 1000) || 0,
          end_data: Math.ceil(this.Dend_time / 1000) || 0
        });
      }
    },

    //表格详情
    async handleDetail(row) {
      this.DetailCheck(row, true, "详情", this.DcurrentPage, this.Dlimit);
      this.initDetail({
        page: this.DcurrentPage,
        limit: this.Dlimit,
        pid: row.uid,
        start_data: Math.ceil(this.Dend_time / 1000),
        end_data: Math.ceil(this.Dstart_time / 1000)
      });
    },

    //表格查看下级
    nextLevel(index, row) {
      this.DetailCheck(row, true, "下级", this.LcurrentPage, this.Llimit);
      this.initLevel({
        pid: row.uid,
        start_data: Math.ceil(this.Dend_time / 1000),
        end_data: Math.ceil(this.Dstart_time / 1000)
      });
    },

    //页容量变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //页码变化
    handleCurrentChange(pageNum) {
      this.currentPage = pageNum;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //详情页容量变化
    DhandleSizeChange(num) {
      this.Dlimit = num;
      this.DcurrentPage = 1;
      this.initDetail({
        page: this.DcurrentPage,
        limit: this.Dlimit,
        pid: this.Pid
      });
    },

    //详情页码变化
    DhandleCurrentChange(pageNum) {
      this.DcurrentPage = pageNum;
      this.initDetail({
        page: this.DcurrentPage,
        limit: this.Dlimit,
        pid: this.Pid
      });
    },

    formateData(res) {
      res.forEach(item => {
        // register_time / cash_time
        item.register_time = this.timestampToTime(item.register_time);
        item.cash_time = this.timestampToTime(item.cash_time);
        item.create_at = this.timestampToTime(item.create_at);
        item.invite_time = this.timestampToTime(item.invite_time);
      });
      return res;
    },

    //十位时间戳转格式日期
    timestampToTime(timestamp) {
      var date = new Date(timestamp * 1000);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },

    DetailCheck(row, visible, title) {
      this.Dstart_time = new Date().getTime() - 60 * 60 * 24 * 1 * 1000;
      this.Dend_time = new Date().getTime();
      this.Pid = row.uid;
      this.visible = visible;
      this.title = title;
    },

    //下级数据获取
    async initLevel(params) {
      let { data } = await this.$http.allAgency.GetAgencyLevel(params);
      let fres = DeepData(data.data);
      this.DtableData = this.formateData(fres);
      this.Dtotal = data.total;
    },

    //详情数据获取
    async initDetail(params) {
      let { data } = await this.$http.allAgency.GetEarnings(params);
      let fres = DeepData(data.data);
      this.DtableData = this.formateData(fres);
      this.Dtotal = data.total;
    },

    //所有玩家数据获取
    async initdata(params) {
      let { data } = await this.$http.allAgency.GetAgency(params);
      let fres = DeepData(data.data);
      this.tableData = this.formateData(fres);
      this.total = data.total;
    }
  }
};



